
const Services = () => {
    const services = [
      {
        title: "Digital Menu",
        description:
          "Make your menu digital edit it easy add or remove items without any additional cost",
        image: "./images/digital-menu.jpg", 
      },
      {
        title: "Online Payment",
        description:
          "Online payment and reserved the money in less than 3 days make your customer order and pay online",
        image: "/images/online-payment.jpg",
      },
      {
        title: "Easy Management",
        description: "Simplifies restaurant management",
        image: "/images/easy-management.jpg",
      },
      {
        title: "Reservation Management",
        description: "Manages table reservations",
        image: "/images/reservation-management.jpg",
      },
      {
        title: "Free Marketing Consultation Call",
        description: "Offers a consultation to grow sales",
        image: "/images/marketing-consultation.jpg",
      },
    ];
  
    return (
      <section className="py-16 bg-gray-50">
        <div className="container mx-auto text-center">
          <h2 className="text-3xl font-bold text-purple-700 mb-10">
            OUR SERVICES
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-2  gap-6 ">
            {services.slice(0, -1).map((service, index) => (
              <div
                key={index}
                className="bg-white p-6 shadow-lg rounded-lg text-center transform transition-transform hover:scale-105"
              >
                <img
                  src={service.image}
                  alt={service.title}
                  className="w-full h-40 object-cover rounded-lg mb-4"
                />
                <h3 className="text-xl font-semibold text-purple-700 mb-2">
                  {service.title}
                </h3>
                <p className="text-gray-600">{service.description}</p>
              </div>
            ))}
          </div>
  
          <div className="flex justify-center mt-6">
            <div className="bg-white p-6 shadow-lg rounded-lg text-center transform transition-transform hover:scale-105">
              <img
                src={services[services.length - 1].image}
                alt={services[services.length - 1].title}
                className="w-full h-40 object-cover rounded-lg mb-4"
              />
              <h3 className="text-xl font-semibold text-purple-700 mb-2">
                {services[services.length - 1].title}
              </h3>
              <p className="text-gray-600">
                {services[services.length - 1].description}
              </p>
            </div>
          </div>
        </div>
      </section>
    );
  };
  
  export default Services