import Head from 'next/head';
import Image from 'next/image'
import heroimg from "./images/heroimg.png"
import heroimg from "./images/heroimg.png"

export default function Home() {
  return (
    <div className="min-h-screen bg-gray-50">
      <Head>
        <title>LookLikes</title>
      </Head>

      <header className="bg-white shadow">
        <div className="container mx-auto py-4 px-6 flex items-center justify-between">
          <div className="text-2xl font-bold text-teal-600">LOOK <span className="text-blue-500">LIKES</span></div>
        </div>
      </header>

      <main className="container mx-auto py-10 px-6">
        
        {/* Hero Section */}

        <section className="flex flex-col md:flex-row items-center gap-10">
          <div className="md:w-1/2 text-center md:text-left">
            <h1 className="text-4xl font-bold text-gray-800">An ERP System to Run Your Entire Business</h1>
            <p className="mt-4 text-gray-600">
              Manage your business, from sales and invoicing, your books, your clientele and workforce to your inventory and operations.
            </p>
            <div className="mt-6 flex justify-center md:justify-start gap-4">
              <button className="bg-teal-600 text-white py-2 px-4 rounded hover:bg-teal-700">Contact Us</button>
              <button className="bg-blue-500 text-white py-2 px-4 rounded hover:bg-blue-600">Watch Video</button>
            </div>
          </div>
          <div className="md:w-1/2 ">
            <div className="rounded-full overflow-hidden shadow-lg ">
              <Image src={heroimg} alt="Decorative" className="absolute right-0 top-0 h-8" />
              <Image src={heroimg2} alt="Decorative" className="absolute right-0 top-0 h-8" />
            </div>
          </div>
        </section>

        {/* About Section */}
        <section className="mt-16">
          <div className="text-center">
            <h2 className="text-3xl font-bold text-teal-600">LOOK <span className="text-blue-500">LIKES</span></h2>
            <p className="mt-4 text-gray-600">
              LookLikes is here to improve the way brands handle their merchandising, sales and promotion efforts by offering a field force
              management solution that optimizes every step of the process, every second of the day.
            </p>
          </div>
        </section>
      </main>

     
    </div>
  );
}
